  --spacing: .1rem;
  --radius: .25rem;
  --max-width: 1170px;
  --fixed-width: 620px;
}

*, :after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: var(--clr-grey-2);
